<script>
import {
  /*validUsername,*/
  isNotEmpty,
  isPhone,
  isIdCard,
  isNumber
} from "@/utils/validate";
import { getAllVehicleColor, getAllVehicleType, getPretrialInput } from "@/api/user";
import { doFileUpload } from "@/api/user";
export default {
  name: "KeyBoarder",
  data() {
    // const validateUsername = (rule, value, callback) => {
    //  if (!validUsername(value)) {
    //    callback(new Error(`Please enter the correct ${rule.field}`));
    //  } else {
    //    callback();
    //  }
    // };
    const validateFirstName = (rule, value, callback) => {
      if (!isNotEmpty(value)) {
        callback(new Error("姓名不能为空!"));
      } else {
        callback();
      }
    };
    const validatePhone = (rule, value, callback) => {
      if (!isPhone(value)) {
        callback(new Error("请输入正确的手机号码！"));
      } else {
        callback();
      }
    };

    const validateIdCard = (rule, value, callback) => {
      if (!isIdCard(value)) {
        callback(new Error(`请输入正确的身份证号码！`));
      } else {
        callback();
      }
    };

    const validateIsNumber = (rule, value, callback) => {
      if (!isNumber(value)) {
        callback(new Error(`请输入正确的格式！`));
      } else {
        callback();
